<template>
	<div id="wrap">
		<div class="top">
			<span class='fan' @click='fan'><</span>
			<p class='song'>{{playList.musicData.songname || playList.albumname}}</p>
			<p class='singer'>{{playList.musicData.singer[0].name || playList.singer[0].name}}</p>
		</div>
		<div class="body">
			<img :src="playList.musicData.newData.images" alt="" :class='imgclass' v-if='!playList.newData'>
			<img :src="playList.newData.images" alt="" :class='imgclass' v-if='playList.newData'>
		</div>
		<p class='songci'>每天都要快乐</p>
		<div class="play">
			<div class="bo">
				<span>{{currentTime}}</span>
				<div class="jindu">
					<p @click='move($event)'></p>
					<span class="btn" ref='move'></span>
				</div>
				<span>{{duration}}</span>
			</div>
			<div class='state'>
				<span class='iconfont icon-shangyishoushangyige' @click='previous()'></span>
				<span class='iconfont' @click='play' :class='{"icon-zanting": playing,"icon-16": !playing}' ref='classname'></span>
				<span class='iconfont icon-048caozuo_xiayishou' @click='next()'></span>
			</div>
		</div>
		<audio :src="playList.musicData.newData.url" ref='music' @timeupdate='go' v-if='!playList.newData'></audio>
		<audio :src="playList.newData.url" ref='music' @timeupdate='go' v-if='playList.newData'></audio>
	</div>
</template>
<script>
	import {mapGetters,mapMutations} from 'vuex';
	export default{
		data(){
			return{
				data: {},
				currentTime: 0,
				duration: 0
			}
		},
		mounted(){
			this.playing?this.$refs.music.play():this.$refs.music.pause();
		},
		watch: {
			playList(){
				this.$nextTick(() => {
					this.$refs.music.play()
				})
			},
			playing(){
				this.playing?this.$refs.music.play():this.$refs.music.pause();
			}
		},
		methods: {
			...mapMutations({
				curIndex1: 'curIndex',
				playing1: 'playing'
			}),
			move(ev){
				this.$refs.move.style.left = ev.offsetX+'px';
				this.$refs.music.currentTime = this.currentTime = ev.offsetX/ev.target.offsetWidth*this.$refs.music.duration;
			},
			//计算播放时间
			getTime(e){
				// console.log(e);
				this.currenttime=Math.floor(this.$refs.music.currentTime);//当前时间
				this.totaltime=Math.floor(this.$refs.music.duration);//总时间


				if(this.currenttimeM<10){//当前时间分钟
					this.currenttimeM=  '0' + Math.floor(this.currenttime / 60);
				}else{
					this.currenttimeM = Math.floor(this.currenttime / 60);
				}
				if(this.currenttimeS < 10){//当前时间秒钟
					this.currenttimeS = '0' + Math.floor(this.currenttime % 60);
				}else{
					this.currenttimeS=Math.floor(this.currenttime % 60);
				}
				if(this.totaltimeM<10){
					this.totaltimeM='0' + Math.floor(this.totaltime / 60);//当前时间分钟
				}else{
					this.totaltimeM=Math.floor(this.totaltime / 60);//当前时间分钟
				}
				if(this.totaltimeS<10){
					this.totaltimeS='0' + Math.floor(this.totaltime % 60);//当前时间秒钟
				}else{
					this.totaltimeS=Math.floor(this.totaltime % 60);//当前时间秒钟
				}
				this.duration = this.totaltimeM+ ':' + this.totaltimeS;
				let str = this.currenttimeM + ':' + this.currenttimeS;
				return str;
			},
			  //返回
			  fan(){
				this.$router.go(-1);
			  },
			//播放
			play(){
				this.playing1(!this.playing);
			},
			//上一首
			next(){
				let index = this.curIndex+1;
				if(index>this.playList.length) index = 0;
				this.curIndex1(index);
				this.playing1(true);
			},
			//上一首
			previous(){
				let index = this.curIndex-1;
				if(index<0) index = 0;
				this.curIndex1(index);
				this.playing1(true);
			},
			//播放中
			go(){
				this.$refs.move.style.left = this.$refs.music.currentTime / this.$refs.music.duration * 100+'%';
				this.currentTime = this.getTime(this.$refs.music.currentTime);
			}
		},
		computed: {
			...mapGetters(['curIndex','curSong','playList','playing']),
			imgclass(){
				return this.playing?'on':'on on1';
			}
		}
	}
</script>
<style scoped>
	#wrap{
		height: 100vh;
		background: #000;
		padding: 0 20px;
		position: relative;
	}
	.on{
		animation: zhuan 5s linear infinite forwards;
	}
	.on1{
		animation-play-state: paused;
	}
	@keyframes zhuan{
		from{
			transform: rotate(0deg);
		}
		to{
			transform: rotate(360deg);
		}
	}

	.top{
		position: relative;
	}
	.top p{
		text-align: center;
		color: #fff;
	}
	.top .singer{
		font-size: 14px;
		margin-top: 20px;
	}
	.top .fan{
		position: absolute;
		left: 5px;
		top: 5px;
		color: rgb(240,185,49);
	}
	.body{
		position: relative;
		margin-top: 20px;
		height: 254px;
	}
	.body img{
		width: 245px;
		border-radius: 50%;
		position: absolute;
		left: 50%;
		margin-left: -122px;
	}
	.songci{
		color: rgb(198,176,193);
		text-align: center;
	}
	.play{
		color: #fff;
		position: absolute;
		bottom: 0;
	}
	.play .bo{
		display: flex;
	}
	.play .jindu{
		position: relative;
		margin: 0 10px;
	}
	.play .jindu p{
		width: 65vw;
		height: 3px;
		background: rgb(61,58,83);
		margin-top: 8px;
	}
	.play .jindu .btn{
		width: 15px;
		height: 15px;
		background: rgb(255,202,50);
		border-radius: 50%;
		position: absolute;
		top: 2px;
		left: 0;
	}
	.play .state{
		display: flex;
		justify-content: center;
		margin-top: 30px;
	}
	.play .state span{
		font-size: 40px;
		margin-right: 25px;
	}
</style>



